<template>
  <!-- add v-if to fix "TypeError: Cannot read property 'isbn' of null" -->
  <!-- refer to https://router.vuejs.org/zh/guide/advanced/data-fetching.html#%E5%AF%BC%E8%88%AA%E5%AE%8C%E6%88%90%E5%90%8E%E8%8E%B7%E5%8F%96%E6%95%B0%E6%8D%AE -->
  <!-- <b-row v-if="getBook"> -->
  <b-row>
    <b-col lg="3" class="mt-3">
      <div class="list-group">
        <li class="list-group-item list-group-item-primary">書目分類</li>
        <TreeMenu :listData="list"></TreeMenu>
      </div>
    </b-col>

    <b-col lg="9">
      <b-form @submit="onSubmit">
        <b-row class="mt-3">
          <b-col md="12">
            <b-card
              header="查詢訂單 (查詢區間為 6 個月)"
              header-text-variant="dark"
              header-tag="header"
              header-bg-variant="muted"
            >
              <b-row class="d-flex align-items-center justify-content-start">
                <b-col md="4" class="">
                  <b-form-datepicker
                    id="startDatepicker"
                    v-bind="labels['zh-TW'] || {}"
                    v-model="start"
                    :min="min"
                    :max="max"
                    :state="getStartDatepickerState"
                    :date-disabled-fn="dateDisabled"
                    @hidden="checkDateRange(false)"
                  ></b-form-datepicker>
                  <!-- <b-input-group class="">
                    <b-input-group-prepend is-text>
                      <b-icon icon="calendar3" aria-hidden="true"></b-icon>
                    </b-input-group-prepend>
                    <b-form-input
                      size="md"
                      id="calendar-start"
                      required
                      placeholder="開始日期"
                    ></b-form-input>
                  </b-input-group> -->
                </b-col>

                <b-col md="1" class="text-center">
                  <span>至</span>
                </b-col>

                <b-col md="4" class="">
                  <b-form-datepicker
                    id="endDatepicker"
                    v-bind="labels['zh-TW'] || {}"
                    v-model="end"
                    :min="min"
                    :max="max"
                    :state="getEndDatepickerState"
                    :date-disabled-fn="dateDisabled"
                    @hidden="checkDateRange(false)"
                  ></b-form-datepicker>
                  <!-- <b-input-group class="">
                    <b-input-group-prepend is-text>
                      <b-icon icon="calendar3" aria-hidden="true"></b-icon>
                    </b-input-group-prepend>
                    <b-form-input
                      size="md"
                      id="calendar-end"
                      required
                      placeholder="結束日期"
                    ></b-form-input>
                  </b-input-group> -->
                </b-col>
                <b-col md="2" class="">
                  <b-button
                    size="sm"
                    block
                    type="submit"
                    class=""
                    variant="outline-primary"
                    >查詢
                  </b-button>
                </b-col>
              </b-row>
            </b-card>
          </b-col>
        </b-row>
      </b-form>

      <b-row class="mt-3" v-if="hasValue">
        <b-col md="12">
          <b-card
            header="訂單內容"
            header-text-variant="dark"
            header-tag="header"
            header-bg-variant="ivory"
          >
            <b-table
              striped
              hover
              stacked="xl"
              ref="selectableTable"
              :fields="fields"
              :items="items"
            >
              <!-- Optional default data cell scoped slot -->
              <template v-slot:cell()="data">
                <span v-text="data.value"></span>
              </template>
              <!-- A custom formatted column -->
              <template v-slot:cell(orderNo)="data">
                <router-link
                  class="text-decoration-none text-primary"
                  :to="'/order/' + data.item.orderNo"
                  target="_blank"
                >
                  <span v-text="data.value"></span>
                </router-link>
              </template>
              <template v-slot:cell(orderStatus)="data">
                <span v-text="getOrderStatus(data.value)"></span>
              </template>
            </b-table>
            <!-- <div class="bg-light"> -->
            <b-pagination
              align="center"
              v-model="currentPage"
              :total-rows="rows"
              :per-page="perPage"
              @input="changePage"
              size="sm"
              first-text="最前頁"
              prev-text="上一頁"
              next-text="下一頁"
              last-text="最末頁"
            ></b-pagination>
            <!-- </div> -->
          </b-card>
        </b-col>
      </b-row>
    </b-col>
  </b-row>
</template>

<script>
import { mapActions } from "vuex";
// import shop from "@/api/shop.js";
import config from "@/common/config";
import TreeMenu from "@/components/TreeMenu.vue";
// import { library } from "@fortawesome/fontawesome-svg-core";
// import SliderThird from "@/components/SliderThird.vue";
// import { faShoppingCart, faMoneyCheckAlt, faHeart } from '@fortawesome/free-solid-svg-icons'
// import {
//   faShoppingCart,
//   faMoneyCheckAlt,
// } from "@fortawesome/free-solid-svg-icons";
// import { faHeart } from "@fortawesome/free-regular-svg-icons";
// import store from "@/store";
import { mapGetters } from "vuex";
// import { ADD_TO_CART, ALREADY_IN_CART } from "@/common/constant";
import { format, subDays, subMonths } from "date-fns";
import ApiService from "@/common/api.service";
import util from "@/common/util";
import { JWT_EXPIRED_MSG, NO_DATA } from "@/common/constant";

// library.add(faShoppingCart, faMoneyCheckAlt, faHeart);

var orders = [
  {
    orderNo: "20201201215051645NNV",
    createTime: "2020-12-02 13:21:21",
    orderStatus: "PENDING",
    payment: "COD",
    amount: 550,
    invoiceNumber: "LZ11439843",
  },
  {
    orderNo: "20201202132121122IMP",
    createTime: "2020-12-01 10:21:21",
    orderStatus: "PENDING",
    payment: "COD",
    amount: 1050,
    invoiceNumber: "KF71399737",
  },
  {
    orderNo: "20201201215051645NNV",
    createTime: "2020-12-02 13:21:21",
    orderStatus: "PENDING",
    payment: "COD",
    amount: 550,
    invoiceNumber: "LZ11439843",
  },
  {
    orderNo: "20201202132121122IMP",
    createTime: "2020-12-01 10:21:21",
    orderStatus: "PENDING",
    payment: "COD",
    amount: 1050,
    invoiceNumber: "KF71399737",
  },
];

export default {
  name: "QueryOrder",
  data() {
    const now = new Date();
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    // const minDate = subDays(today, 180);
    const minDate = subMonths(today, 6);

    return {
      list: config.treeMenu,
      fields: config.queryOrderFields,
      items: [],
      // items: orders,
      start: "",
      end: "",
      startDate: "",
      endDate: "",
      labels: {
        "zh-TW": {
          weekdayHeaderFormat: "narrow",
          labelPrevDecade: "過去十年",
          labelPrevYear: "上一年",
          labelPrevMonth: "上個月",
          labelCurrentMonth: "當前月份",
          labelNextMonth: "下個月",
          labelNextYear: "明年",
          labelNextDecade: "下一個十年",
          labelToday: "今天",
          labelSelected: "選定日期",
          labelNoDateSelected: "未選擇日期",
          labelCalendar: "日曆",
          labelNav: "日曆導航",
          labelHelp: "使用游標鍵瀏覽日期",
        },
      },
      min: minDate,
      max: today,
      errors: [],
      startDatepickerState: null,
      endDatepickerState: null,
      rows: 100,
      perPage: 10,
      currentPage: 1,
    };
  },
  props: {},
  components: {
    TreeMenu,
    // SliderThird,
  },
  mounted() {},
  methods: {
    fetchData(page) {
      ApiService.setHeader();
      let params = {
        // separate datepicker value and api parameter
        // this.start is for startDatepicker, this.end is for endDatepicker
        // this.startDate and this.endDate are for api parameters
        params: {
          // startDate: this.start,
          // endDate: this.end,
          startDate: this.startDate,
          endDate: this.endDate,
          page: page,
        },
      };
      ApiService.query("order/query", params)
        .then((response) => {
          console.log(
            "QueryOrder.vue onSubmit response : " + JSON.stringify(response)
          );
          ApiService.deleteHeader();
          this.items = JSON.parse(JSON.stringify(response.data.orders));
          this.rows = response.data.rows;
          this.currentPage = page;
          if (this.items.length == 0) {
            this.$store.dispatch("modal/setModalMsg", NO_DATA);
            this.$store.dispatch("modal/setModalShow", true);
          }
        })
        .catch((err) => {
          console.log("err : " + err);
          console.log("err JSON.stringify : " + JSON.stringify(err));
          console.log("err.response : " + JSON.stringify(err.response));
          ApiService.deleteHeader();
          if (err.response.data.message) {
            console.log(
              "err.response.data.message : " + err.response.data.message
            );
            let msg = util.checkJWTExpired(err, this);
            // if (util.testJWTExpiredMsg(err.response.data.message)) {
            //   msg = JWT_EXPIRED_MSG;
            //   this.$store.dispatch("auth/purgeAuth");
            //   console.log("this.$route.path", this.$route.path);
            //   // https://router.vuejs.org/zh/guide/essentials/navigation.html
            //   this.$router.push({
            //     path: "login",
            //     query: { redirect: this.$route.path },
            //   });
            // } else {
            //   msg = err.response.data.message;
            // }
            this.$store.dispatch("modal/setModalMsg", msg);
            this.$store.dispatch("modal/setModalShow", true);
          }
        });
    },
    onSubmit(event) {
      event.preventDefault();
      this.clearErrors();
      this.checkDateRange(true);
      if (!this.start.length) {
        this.errors.push("請選擇開始日期");
        this.startDatepickerState = false;
      }
      if (!this.end.length) {
        this.errors.push("請選擇結束日期");
        this.endDatepickerState = false;
      }

      if (this.errors.length) {
        this.showErrors();
      } else {
        // separate datepicker value and api parameter
        // this.start is for startDatepicker, this.end is for endDatepicker
        // this.startDate and this.endDate are for api parameters
        this.startDate = this.start;
        this.endDate = this.end;
        this.fetchData(1);
      }
    },
    // https://bootstrap-vue.org/docs/components/form-datepicker#disabling-dates
    // ymd The date as a YYYY-MM-DD string
    // date The date as a Date object
    dateDisabled(ymd, date) {
      let today = new Date();
      let nowYear = today.getFullYear();
      let nowMonth = today.getMonth();
      let nowDate = today.getDate();
      // console.log(nowYear, nowMonth, nowDate);
      let currentDate = new Date(nowYear, nowMonth, nowDate, 23, 59, 59, 999);
      return date.getTime() > currentDate.getTime();
    },
    checkDateRange(isSubmitted) {
      console.log("checkDateRange");
      // clear this.items
      // this.items.splice(0, this.items.length);
      this.clearErrors();
      if (this.start.length) {
        this.startDatepickerState = null;
      }
      if (this.end.length) {
        this.endDatepickerState = null;
      }
      if (!!this.start.length && !!this.end.length) {
        let startArray = this.start.split("-");
        console.log("startArray", JSON.stringify(startArray));
        // https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Global_Objects/Date
        // https://www.fooish.com/javascript/date/
        // JavaScript 表達月份 (month) 是從 0 到 11，0 是一月；11 是十二月。
        let startDate = new Date(
          parseInt(startArray[0]),
          parseInt(startArray[1]) - 1,
          parseInt(startArray[2])
        );
        console.log("startDate", startDate);
        let endArray = this.end.split("-");
        console.log("endArray", JSON.stringify(endArray));
        let endDate = new Date(
          parseInt(endArray[0]),
          parseInt(endArray[1]) - 1,
          parseInt(endArray[2])
        );
        console.log("endDate", endDate);
        // https://bootstrap-vue.org/docs/components/form-datepicker#comp-ref-b-form-datepicker-props
        // `true` for valid, `false` for invalid, or `null` for no validation state
        if (startDate.getTime() > endDate.getTime()) {
          this.startDatepickerState = false;
          this.endDatepickerState = false;
          // this.$toastr.e("日期區間有誤，請重新設定!!");
          this.errors.push("日期區間有誤，請重新設定!!");
        } else {
          this.startDatepickerState = null;
          this.endDatepickerState = null;
          this.clearErrors();
        }
      }
      if (this.errors.length && !isSubmitted) {
        this.showErrors();
      }
    },
    showErrors() {
      this.errors.forEach((ele) => {
        this.$toastr.e(ele);
      });
    },
    clearErrors() {
      // https://stackoverflow.com/questions/1232040/how-do-i-empty-an-array-in-javascript
      // https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Global_Objects/Array/splice
      this.errors.splice(0, this.errors.length);
    },
    changePage(page) {
      console.log("changePage", page);
      this.fetchData(page);
    },
    getOrderStatus(status) {
      let orderStatus;
      switch (status) {
        case "PENDING":
          orderStatus = "待處理";
          break;
        case "PROCESSING":
          orderStatus = "處理中";
          break;
        case "SHIPPED":
          orderStatus = "運送中";
          break;
        case "RETURNED":
          orderStatus = "已退貨";
          break;
        case "CANCELED":
          orderStatus = "已取消";
          break;
        default:
          orderStatus = "未知";
          break;
      }
      return orderStatus;
    },
  },
  watch: {
    // https://router.vuejs.org/zh/guide/advanced/data-fetching.html
    // 如果路由有变化，会再次执行该方法
    // $route: "fetchData",
  },
  created() {
    // console.log("created() isbn: " + this.$route.params.isbn);
    // this.fetchData();
  },
  mounted: function () {
    // if (this.$store.getters["products/getBook"]){
    // this.book = this.$store.getters["products/getBook"];
    // }
  },
  beforeDestroy() {
    // console.log("beforeDestroy clearTimeout", +this.timeoutID);
    // clearTimeout(this.timeoutID);
  },
  computed: {
    getStartDatepickerState() {
      return this.startDatepickerState;
    },
    getEndDatepickerState() {
      return this.endDatepickerState;
    },
    hasValue() {
      return this.items.length != 0;
    },
    // ...mapGetters("products", ["getBook"]),
  },
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
/* https://www.w3schools.com/colors/colors_hex.asp */
.bg-AliceBlue {
  background-color: AliceBlue !important;
}
.bg-ivory {
  background-color: ivory !important;
}
</style>
